import React from 'react'
import styles from './App.module.css'

import {
    Card, Button,  CardTitle,  
    CardSubtitle, CardBody
  } from 'reactstrap';



  const Person = ({ click, name ,roll }) => <Card style={{shadowColor: "#000",
  shadowOffset: {
    width: '0',
    height: '12'
  },
  shadowOpacity: '0.58',
  shadowRadius: '16.00',
  
  elevation: 24,fontFamily:'courier new',borderRadius:"50px",width:"40%",marginLeft:"30%",textAlign:"center",backgroundColor:"#404040",color:"white"}}>
  <CardBody>
    <CardTitle style={{fontWeight:"bolder"}}>PDS Lab</CardTitle>
    <CardSubtitle >{name}<br/>
    {roll}</CardSubtitle>
    
    <Button onClick={click} style={{fontFamily:"courier new",fontWeight:"bolder"}}>De-Register</Button><br/>
  </CardBody>
  </Card>

class Pds extends React.Component {

  state = {
    people: [
      { id: 1, name: 'Matka',roll:"PDS-01" },
      { id: 2, name: 'Sravya' , roll:"PDS-02"},
      { id: 3, name: 'Samanvitha',roll:"PDS-03" },
      {id:4, name:"Arush",roll:"PDS-04"},
      {id:5,name:"Samanyu",roll:"PDS-05"},
      {id:6,name:"Chelesy",roll:"PDS-06"}
    ]
  }

  deletePerson = (i) => {
    const lemon = [...this.state.people]
    lemon.splice(i, 1)
    this.setState({ people: lemon })
  }


  render() {


    return (
      <div className={styles.bg}>
        <br/>
        <br/>
        <br/>
        {this.state.people.map((person, i) => {
          return <Person 
            key={person.id}
            click={() => this.deletePerson(i)}
            name={person.name}
            roll={person.roll} />
        }
        )}

      <Button className={styles.btn}><span>Add More</span></Button>
      </div>
    )
  }
}
export default Pds
