import React from 'react';
import Aux from './Aux.js';
import Home from './components/Home/Home.js';
import Navbar from "./components/Navbar/Navbar.js"
import Subjects from './components/Subjects/Subjects.js'
import Algo from './components/Participants/Algo.js'
import Pds from './components/Participants/Pds.js'
import Oper from './components/Participants/Oper.js'
import Software from './components/Participants/Software.js'
import Networks from './components/Participants/Networks.js'
import {BrowserRouter as Switch, Route, BrowserRouter } from 'react-router-dom';


function App(){
  return (
      <Aux>
        <BrowserRouter>
    <Navbar />
    <Switch>
    <Route path='/home' exact component={Home}/>
    <Route path='/subjects' exact component={Subjects}/>  
    <Route path='/pds' exact component={Pds} />
    <Route path='/algo' exact component={Algo} />
    <Route path='/network' exact component={Networks} />
    <Route path='/soft'exact  component={Software} />
    <Route path='/oper'exact component={Oper} />
    </Switch>
    </BrowserRouter>
    </Aux>
    
  )
}
export default App;