import React from 'react'
import styles from './App.module.css'

import {
    Card, Button,  CardTitle,  
    CardSubtitle, CardBody
  } from 'reactstrap';



  const Person = ({ click, name ,roll }) => <Card style={{fontFamily:'courier new',borderRadius:"50px",width:"40%",marginLeft:"30%",textAlign:"center",backgroundColor:"#404040",color:"white"}}>
  <CardBody>
    <CardTitle style={{fontWeight:"bolder"}}>O.S. Lab</CardTitle>
    <CardSubtitle >{name}<br/>
    {roll}</CardSubtitle>
    
    <Button onClick={click} style={{fontFamily:"courier new",fontWeight:"bolder"}}>De-Register</Button><br/>
  </CardBody>
  </Card>

class Oper extends React.Component {

  state = {
    people: [
      { id: 1, name: 'Preston',roll:"OS-01" },
      { id: 2, name: 'Christine' , roll:"OS-02"},
      { id: 3, name: 'Plump',roll:"OS-03" },
      {id:4, name:'Matt',roll:"OS-04"},
      {id:5,name:'Sammuel',roll:"OS-05"},
      {id:6,name:'Claudie',roll:"OS-06"}
    ]
  }

  deletePerson = (i) => {
    const lemon = [...this.state.people]
    lemon.splice(i, 1)
    this.setState({ people: lemon })
  }

  render() {

    return (
      <div className={styles.bg}>
      <br/>
      <br/>
      <br/>
      
        {this.state.people.map((person, i) => {
          return <Person 
            key={person.id}
            click={() => this.deletePerson(i)}
            name={person.name}
            roll={person.roll} />
        }
        )}

      <Button className={styles.btn}><span>Add More</span></Button>
      </div>
    )
  }
}
export default Oper
